*{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 3rem;

}
body{
  height: 100%;
  background-color: black;
}
h1{
  text-align: center;
  color: violet;
  font-family: 'Roboto', sans-serif;

}
.f1{
  color: lime;
}
.inputs{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 3rem;
}

.inputs .input{
  padding: 0.5rem 1rem;
  height: 50px;
  outline: none;
  background-color: white;
  widows: 450px;
  font-size: 1.15rem;
  margin: 0.25rem;
  border-radius: 25px;
  border: none;
}
.btn_add{
  height: 50px;
  width: 50px;
  border-radius: 25px;
  outline: none;
  border: none;
  background-color: white;
  color: white;
  margin: 0.25px;
  background-color: lime;
  cursor: pointer;
}
.btn_add:hover{
  opacity: 0.7;
}