*{
    padding: 0;
    margin:0;
    box-sizing: border-box;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 3rem;

}

.con{
    padding: 0.5rem;
    margin-bottom: 1.5rem;
    border-bottom: 4px solid white;
}
.list{
    background: none;
    outline: none;
    color: white;
    border: none;
    widows: 350px;
    font-size: 1.4rem;
}
.btn_edit{
    font-size: 1.4rem;
    margin: 0 0.5rem;
    font-family: 'Roboto', sans-serif;
    background: none;
    outline: none;
    color: lime;
    border: none;
}
.btn_delete{
    font-size: 1.4rem;
    margin: 0 0.5rem;
    font-family: 'Roboto', sans-serif;
    background: none;
    outline: none;
    color: red;
    border: none;
    cursor: pointer;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 1.4rem;
}
.list p input:focus{
    outline: none;
}